import { login } from '@/api/login.js'

export default {
	data() {
		return {
			hideEyes: false,
			loginName: '',
			password: ''
		}
	},
	methods: {
		passwordF_B() {
			this.hideEyes = !this.hideEyes;
		},
		formSubmit(e) {	
			console.log(this.loginName);
		// uni.switchTab({
		// 	url:'../menu/menu'
		// })
			login({loginName: this.loginName,password:this.password}).then(res => {
			 getApp().globalData.vueSessionId = res.sessionId
			 console.log(getApp().globalData.vueSessionId)
			 if(res.reCode === '0'){
				 uni.switchTab({
				 	url:'../menu/menu'
				 })
		
			 }else{
				 uni.showModal({
				     title: '提示',
				     content: '登录信息错误',
				     success: function (res) {
				         if (res.confirm) {
				             console.log('确定');
				         } else if (res.cancel) {
				             console.log('取消');
				         }
				     }
				 });
			 }

		}).catch(err => {
			
		})
		}
	}
}
